import React, { useState, useRef } from "react";
import './App.css'
import data from './data.json'

let pokemonData = [];
let ids = [];

data.map( i => {
  if(!ids.includes(i.pokemon_id)) {
    ids.push(i.pokemon_id);
    pokemonData.push(i);
  }
  return null;
})

function App() {
  const [name, setName] = useState(""); // string
  const [pokemons, setPokemons] = useState(""); // string
  const [selectedPokemon, setSelectedPokemon] = useState(null); // null || object
  const [isHoverLocked, setIsHoverLocked] = useState(false); // bool
  
  const foundDiv = useRef();
  const dummy = useRef();
  
  //stuff
  //
  //components

  const SelectedPokemon = () => {
    if(selectedPokemon != null) {
      return (
        <div className="selected-pokemon" ref={ dummy }>
          <p id="name">Nameü§î: { selectedPokemon.pokemon_name }</p>
          <p id="id">Idüî¢: { selectedPokemon.pokemon_id }</p>
          <p id="atk">Base attackü•ä:{ selectedPokemon.base_attack }</p>
          <p id="def">Base defenseüõ°:{ selectedPokemon.base_defense }</p>
          <p id="sta">Base stamina‚ù§:{ selectedPokemon.base_stamina }</p>
        </div>
      )
    } else {
      return null;
    }
  }

  const FoundPokemon = () => {
    if(pokemons) {
      return (pokemons.map( arg => (
        <p key={ arg.unique_id } className={ arg === selectedPokemon ? "pokemon-found locked" : "pokemon-found" } onMouseEnter={ e => handleHoverEvent(e, arg) } onClick={ e => handleClickEvent(e, arg) }>{ arg.pokemon_name }</p>
      )))
    };
    return <></>;
  };
                  
  const LockedSection = () => {
    return (
      <section className="locked-section">
        { isHoverLocked ? <ion-icon class="md hydrated lock-icon" name="lock-closed-outline"></ion-icon> : <ion-icon class="md hydrated lock-icon" name="lock-open-outline"></ion-icon> }
      </section>
    );
  }

  //components
  //
  //functions

  const handleSearchEvent = e => {
    e.preventDefault();
    if(name === "") {
       setPokemons("")
       foundDiv.current.style.visibility = "collapse";
    } else {
      let temp = pokemonData.filter( arg => arg.pokemon_name.toUpperCase().startsWith(name.toUpperCase()));
      setPokemons(temp);
      
      foundDiv.current.style.visibility = temp.length > 0 ? "visible" : "collapse";
      if(temp.length === 0) {
        setSelectedPokemon(null);
        setIsHoverLocked(false);
      }
    }
  }

  const handleClearButton = () => {
    setPokemons(null);
    setSelectedPokemon(null);
    setName("");
    setIsHoverLocked(false);
    foundDiv.current.style.visibility = "collapse";
  }

  const handleHoverEvent = (e, arg) => {
    if(!isHoverLocked) {
      setSelectedPokemon(arg);
    }
  }

  const handleClickEvent = (e, arg) => {
    if(isHoverLocked && arg.unique_id !== selectedPokemon.unique_id) {
      setSelectedPokemon(arg);
      e.target.scrollIntoView({ behavior: 'smooth', block: 'center' });
    } else {
      setIsHoverLocked(isHoverLocked ? false : true);

    }
  }

  const handleScrollButtonEvent = () => {
    try {
      dummy.current.scrollIntoView({ behavior: 'smooth', block: 'center' });
    } catch {
      return;
    }
  }
  
  //functions
  //
  //react dom
  
  return (
    <>
      <button onClick={ handleScrollButtonEvent } className="scroll-into-view">
        <ion-icon name="arrow-up-outline"></ion-icon>
      </button>


    <div className="search-wrapper">

      <form className="search-form" onSubmit={ handleSearchEvent }>

        <input value={ name } onChange={ e => { setName(e.target.value) } } type="text" placeholder="Pokemon name" className="pokemon-name" />
        <input type="submit" value="Search" />

      </form>
      <SelectedPokemon/>

      <div ref={ foundDiv } className="pokemon-found-wrapper">
        <FoundPokemon pokemons={ pokemons }/>
      </div>
      <button className="clear-button" onClick={ handleClearButton }>Clear</button>

    </div>
    <LockedSection/>
    </>
  );
}

export default App;
